:root {
  --pink-bg: #ffb1af;
  --pink-accent: #ffd1d1;
  --white: #ffffff;
  --yellow-bg: #f9e96e;
}

* {
  box-sizing: border-box;
  margin: 0 !important;
  padding: 0 !important;
}

*:focus {
  outline: none;
}

img {
  width: 100%;
  height: auto;
  line-height: 0;
  display: block;
}

a {
  text-decoration-line: none;
}

ul {
  list-style-type: none;
}

button {
  cursor: pointer;
  display: block;
  border: none;
}

body {
  font-family: "Baloo Tammudu 2";
}

.container {
  max-width: 1210px;
  width: 100%;
  padding: 0 15px;
  outline: 3px dashed red;
  margin: 0 auto !important;
}

.hero {
  position: relative;
  padding: 261px 0 !important;
  background-color: var(--pink-bg);
}

.hero__img {
  position: absolute;
  height: 100%;
  width: auto;
  left: 0;
  top: 0;
}

.hero__text {
  margin-left: auto !important;
  max-width: 680px;
}

.hero__title {
  color: var(--white);
  font-size: 64px;
  font-weight: 800;
  line-height: 1.5;
}

.hero__pink {
  color: var(--pink-accent);
}

.hero__yellow {
  color: var(--yellow-bg);
}

.hero__desc {
  color: var(--white);
  font-size: 32px;
  font-weight: 400;
  line-height: 1.5;
}
